import React, { Component } from 'react';
import SEO from "../components/seo"
import simpleParallax from 'simple-parallax-js';
import BlogList from '../components/blogList'
import importBlogs from '../utils/importBlogs';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs: []
        }
    }

    componentDidMount() {
        var image = document.getElementById('vrc__homeImg');
        new simpleParallax(image, {
            scale: 1.8,
            delay: .6,
            transition: 'cubic-bezier(0,0,0,1)'
        });
        importBlogs((blog) => {
            this.setState((state) => { return { blogs: state.blogs.concat([blog]) } })
        });
    }

    render() {
        const { blogs } = this.state;
        return (
            <>
                <SEO title="Home" />
                <div className="vrc__homeImgContainer">
                    <img src="../../home.jpg" id="vrc__homeImg" />
                </div>
                <div className="container">
                    <br />
                    <h1>Welcome to VrC site.</h1>
                    <br />
                    <p>How you'll enjoy :)</p>
                </div>
                <BlogList blogs={blogs} />
            </>
        )
    }
}

export default Home;